.business {
  &-content {
    margin-top: 360px;
    margin-right: 91px;
    position: relative;
    z-index: 4;
  }

  //жовта фігура перед блоком текста
  &-content::before {
    content: '';
    position: absolute;
    width: 178px;
    height: 178px;
    left: -46px;
    top: -60px;
    display: inline-block;
    background-color: $img-after-bgc;
    border-radius: 0px 0px 100px 0px;
    transform: rotate(-180deg);
    z-index: -1;
  }

  &-wrapper {
    margin-top: 94px;
    display: flex;
    position: relative;
  }

  //сині точки під блоком текста
  &-wrapper::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 68px;
    left: 113px;
    height: 154px;
    width: 404px;
    background-size: 19px 20px;
    background-repeat: repeat;
    background-position: 40.4px 17.4px;
    background-image: radial-gradient(circle, $dots-color-business 3.5px, transparent 3.5px),
      radial-gradient(circle, $dots-color-business 3.5px, transparent 3.5px);
    z-index: -40;
  }
  &-content h2 {
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 49px;
    margin-bottom: 30px;
  }

  &-content p {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: $seconday-text-color;
  }

  &-block-section {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    min-width: 650px;
    z-index: 0;
  }

  // блакитний фон під бізнес-блоком
  &-block-section::before {
    content: '';
    width: 953px;
    height: 619px;
    background-color: $business-block-bgc;
    border-radius: 150px 0px 0px 0px;
    position: absolute;
    top: 145px;
    left: -160px;
    z-index: -4;
  }

  //рожеве коло після бізнес-блока
  &-block-section::after {
    content: '';
    position: absolute;
    width: 120px;
    height: 120px;

    border: 8.8px solid #ff007a;
    border-radius: 100%;
    position: absolute;
    top: 730px;
    left: 580px;
    z-index: -5;
  }

  &-block {
    background-color: #ffffff;
    padding: 39px 32px 57px;
    text-align: center;
    width: 308px;
    height: 379px;
    border: 1px solid $business-block-border;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.05);
    border-radius: 40px;
  }

  &-block:nth-child(odd) {
    margin-right: 34px;
  }
  &-block:first-child {
    margin-top: 79px;
    margin-bottom: 30px;
  }

  &-block:last-child {
    margin-top: -78px;
  }

  &-block h3 {
    margin-bottom: 27px;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
  }

  &-block p {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
  }

  // обгортка для СВГ-іконок
  &-svg {
    text-align: center;
    width: 121px;
    height: 121px;
    margin: 0 auto;
    border-radius: 20px;
    margin-bottom: 31px;
  }
}

//розміри СВГ-іконок
.svg-items {
  margin: 24px;
  width: 74px;
  height: 74px;
}

//фон для СВГ-іконок
.box-search-bg {
  background-color: $bgc-color-box;
}

.empty-wallet-bg {
  background-color: $bgc-color-wallet;
}

.code-bg {
  background-color: $bgc-color-code;
}

.analysis-bg {
  background-color: $bgc-color-analysis;
}
